"use client"

import { useState } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { useRouter } from 'next/navigation'

export default function Login() {
  const [credentials, setCredentials] = useState({
    username: '',
    password: ''
  })
  const [error, setError] = useState('')
  const router = useRouter()

  const handleLogin = () => {
    // Simular autenticação
    if (credentials.username === 'admin' && credentials.password === 'admin123') {
      localStorage.setItem('userRole', 'admin')
      localStorage.setItem('userName', 'Administrador')
      router.push('/')
    } else if (credentials.username === 'joao' && credentials.password === 'joao123') {
      localStorage.setItem('userRole', 'tecnico')
      localStorage.setItem('userName', 'João Silva')
      router.push('/')
    } else if (credentials.username === 'maria' && credentials.password === 'maria123') {
      localStorage.setItem('userRole', 'tecnico')
      localStorage.setItem('userName', 'Maria Santos')
      router.push('/')
    } else {
      setError('Usuário ou senha incorretos')
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-orange-50 to-white flex items-center justify-center p-6">
      <Card className="w-full max-w-md border-orange-200">
        <CardHeader className="text-center">
          <CardTitle className="text-2xl text-orange-800">Sistema de Controle de Frota</CardTitle>
          <CardDescription>Faça login para acessar o sistema</CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div>
            <Label htmlFor="username">Usuário</Label>
            <Input
              id="username"
              value={credentials.username}
              onChange={(e) => setCredentials({...credentials, username: e.target.value})}
              placeholder="Digite seu usuário"
              className="focus:ring-orange-500 focus:border-orange-500"
            />
          </div>
          <div>
            <Label htmlFor="password">Senha</Label>
            <Input
              id="password"
              type="password"
              value={credentials.password}
              onChange={(e) => setCredentials({...credentials, password: e.target.value})}
              placeholder="Digite sua senha"
              className="focus:ring-orange-500 focus:border-orange-500"
            />
          </div>
          {error && (
            <div className="text-red-600 text-sm text-center">
              {error}
            </div>
          )}
          <Button 
            onClick={handleLogin}
            className="w-full bg-orange-500 hover:bg-orange-600 text-white"
          >
            Entrar
          </Button>
          
          <div className="mt-6 p-4 bg-orange-50 rounded-lg">
            <h3 className="font-semibold text-orange-800 mb-2">Usuários de Teste:</h3>
            <div className="text-sm text-gray-600 space-y-1">
              <div><strong>Admin:</strong> admin / admin123</div>
              <div><strong>Técnico João:</strong> joao / joao123</div>
              <div><strong>Técnico Maria:</strong> maria / maria123</div>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
